import React, { ComponentProps, ReactNode } from 'react';
import { Button, makeStyles, Theme, Box } from '@material-ui/core';
import { useCountDown } from 'src/hooks/useCountDown';
import { useNavigate } from '@reach/router';

const useStyles = makeStyles<Theme>(theme => ({
  wrapper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  button: {
    marginBottom: theme.spacing(2),
  },
  link: {
    textDecoration: 'none',
  },
}));

interface Props extends ComponentProps<typeof Button> {
  children?: ReactNode;
  expiry: Date;
}
const MentoringApplyButton = ({
  expiry,
  className,
  style,
  children = '멘토링 프로젝트 신청하기',
  href,
  onClick,
  ...rest
}: Props) => {
  const navigate = useNavigate();
  const styles = useStyles();
  const { expired } = useCountDown(expiry);
  const buttonText = expired ? '마감되었습니다' : children;

  return (
    <Box display="flex" flexDirection="column" alignItems="center">
      <Button
        disabled={expired}
        className={`${styles.button} ${className}`}
        onClick={e => {
          onClick?.(e);
          if (!expired) {
            navigate(href ?? '/');
          }
        }}
        {...rest}
      >
        {buttonText}
      </Button>
    </Box>
  );
};

export default MentoringApplyButton;
